.container {
    margin-top: 3.75rem;
    min-width: 50rem;
    height: 18.75rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    position: relative;

}   
.houseDiv{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.houseDiv span{
    margin-bottom: 1.875rem;
    color: white;
    font-size: 1.5rem;
}
.selected{
    position: relative;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    transform: scale(1.7);
}

.selected:hover{
    transform: scale(1.7);  
}

.house{
    background-color: rgba(0, 0, 0, 0.237);
    width: 17rem;
    height:  17rem;
    display: flex;  
    justify-content: center;
    align-items: center;
    border-radius: 50%;
}

.countDown{
    font-size: 115px;
    font-weight: 700;
    color: white;    
}

.mid{
    min-width: 18.75rem;    
    height: inherit;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.mid .win{
    margin-top: -1.25rem;
    font-size: 3rem;
    font-weight: 800;
    color: white;  
    text-align: center;
}

.mid button {
    margin-top: -1.25rem;
    width: 7.5rem;
    height: 2.8125rem;
    font-size: .9375rem;
    font-weight: 400;
    color: rgb(89, 40, 89);
    border-radius: .5rem;
    transition: ease-in all 0.1s;
    border: none;
}

.mid button:hover{
    color: rgb(255, 54, 54);
    transition: ease-in all 0.1s;
}

.winnerStyle{
    box-shadow: inset 0 5px 6px gray, 0 8px #a46e0a, 0 0 0 50px #ffffff12, 0 0 0 100px #ffffff0d, 0 0 0 150px #ffffff06;
}

@media screen and (max-width:600px) {

    .container{
        max-width: 25rem ;
        width: 25rem;
        min-width: 25rem;
        min-height: 15.625rem;
        height: 15.625rem;
        justify-content: space-around;
        align-items: center;
    }
    .mid{
        min-width: 150px;
        max-width: 6.25rem;
    }
    .selected{
        transform: scale(1); 
    }
    .house{
        background-color: rgba(0, 0, 0, 0.237);
        width: 6.875rem;
        height: 6.875rem;
        display: flex;  
        justify-content: center;
        align-items: center;
        border-radius: 50%;
    }

    .mid .win{
        margin-top: 0;
        font-size: 1.5rem;
    }
    
    .mid button {
        margin-top: -1.25rem;
        width: 7.5rem;
        height: 35px;
        font-size: .9375rem;
        font-weight: 400;
        color: rgb(89, 40, 89);
        border-radius: .5rem;
        transition: ease-in all 0.1s;
        border: none;
    }
    .countDown{
        font-size: 2.5rem;
    }

    .winnerStyle{
        box-shadow: inset 0 5px 6px gray, 0 8px #a46e0a, 0 0 0 20px #ffffff12, 0 0 0 40px #ffffff0d, 0 0 0 60px #ffffff06;
    }

    .houseDiv span{
        margin-bottom: 1.25rem;
        color: white;
        font-size: 1rem;
    }

}